<template>
    <div class="shopCar">
      <!-- 头部 -->
      <van-nav-bar title="购物车" >
        <template #right>
          <van-icon name="service-o" size="30" />
        </template>
      </van-nav-bar>
    
      <!-- 商品信息 -->
      <input type="checkbox" class="checkOne" >
      <van-card
          :num="num1"
          :price="price1"
          desc="缓解头痛 感冒"
          title="布洛芬缓释胶囊"
          :thumb="medicine1"
        >
          <template #tags>
            <van-tag plain type="danger">优惠购药</van-tag>
          </template>
          <template #footer>
            <van-button size="mini" @click="del1">-</van-button>
            <van-button size="mini" @click="add1">+</van-button>
          </template>
      </van-card>

      <input type="checkbox" class="checkTwo" v-model="isClick">
      <van-card
          :num="num2"
          :price="price2"
          desc="医院常用 家庭常用"
          title="消毒水"
          :thumb="medicine2"
        >
          <template #tags>
            <van-tag plain type="danger">优惠购药</van-tag>           
          </template>
          <template #footer>
            <van-button size="mini" @click="del2">-</van-button>
            <van-button size="mini" @click="add2">+</van-button>
          </template>
      </van-card>

      <!-- 商品结算 -->
      <van-submit-bar :price="sum" button-text="提交订单" @submit="onSubmit">
        <!-- <van-checkbox v-model="checked">全选</van-checkbox> -->
        <template #tip>
          你的收货地址不支持同城送, <span @click="onClickEditAddress">修改地址</span>
        </template>
      </van-submit-bar>
      
      <!-- 密码输入框 -->
      <!-- <van-password-input
        :value="value"
        info="密码为 6 位数字"
        :focused="showKeyboard"
        @focus="showKeyboard = false"
      /> -->
      <!-- 数字键盘 -->
      <!-- <van-number-keyboard
        :show="showKeyboard"
        @input="onInput"
        @delete="onDelete"
        @blur="showKeyboard = false"
      /> -->
      <!-- 吸底导航 -->
      <footer-bar></footer-bar>
        
        


    </div>
</template>

<script>
import FooterBar from '@/components/FooterBar.vue'

  export default {
      data () {
        return {
          medicine1:require("../assets/img/medicine/02.png"),
          medicine2:require("../assets/img/medicine/01.png") ,
          checked:true  ,    
          num1:1 ,          
          num2:1 ,
          price1:14.50,         
          price2:16.5, 
          sum: "" ,  
          value: '123',
          showKeyboard: true,  
          isClick:true 
        }
      },
      components: {
        FooterBar
      },
      methods: {
        onClickEditAddress(){

        },
        onSubmit(){
          this.$dialog.alert({
          title: "温馨提示", //加上标题
          message: "请仔细确认金额后支付", //改变弹出框的内容
          img:'../assets/img/账户.png'
          // showCancelButton: true //展示取水按钮
          }).then(() => { //点击确认按钮后的调用
            this.$toast({
              icon: require("../assets/img/zhiFu.jpg")
            })
          })
          this.sum = 0
          this.isClick = false
        },
        add1() {
          this.num1++
          this.sum = (this.num1 * this.price1 + this.num2 * this.price2)*100
        },
        del1() {
          let num1 = this.num1--
          this.sum = (this.num1 * this.price1 + this.num2 * this.price2)*100
        },
        add2() {
          this.num2++
          this.sum = (this.num1 * this.price1 + this.num2 * this.price2)*100

        },
        del2() {
          let num2 = this.num2--
          this.sum = (this.num1 * this.price1 + this.num2 * this.price2)*100

        },
      },
      mounted () {
        this.sum = (this.num1 * this.price1)*100
      }
  }
</script>

<style lang="less" scoped>
  .van-nav-bar{
    background-color: #80d4fd;
    height: 1rem;
    line-height: 1rem;
    .van-icon{
      color: black;
    }
  }
  /deep/.van-card{
    border: 1px solid #e6e9ec;
    border-radius:.5rem
  }
  /deep/ .van-nav-bar__title{
    font-size: .4rem;
  }
  /deep/ .van-card__title {
    font-size: .26rem;
  }
  /deep/ .van-button--mini{
    font-size: .3rem;
  }
  /deep/ .van-image__img{
    width: 80%;
  }
  
  .checkOne{
    display: block;
    position: absolute;
    bottom: .9rem;
    // left: .05rem;
    z-index: 999;
    // margin-top: 1rem;
  }
  .checkTwo{
    display: block;
    position: absolute;
    bottom: 3.2rem;
    // left: .05rem;
    z-index: 999;
    // margin-top: 1rem;
  }
  .shopCar{
    position: relative;
  }

  /deep/.van-submit-bar{
    margin-bottom: .9rem;
  }
   
  /deep/ .van-icon__image {
    width: 3rem;
    height: 3rem;
    object-fit: contain;
  }
  /deep/ .van-icon .van-toast__icon{
    width: 7rem;
    height: 6rem;
  }
   
</style>
