<template>
    <div class="publish">
        <!-- 导航栏 -->
        <van-nav-bar title="发布动态" left-text="返回" left-arrow @click-left="onClickLeft">
            <template #right>
                <van-icon name="search" size="30" />
            </template>
        </van-nav-bar>
        <!-- 内容输入 -->
        <van-form @submit="onSubmit">
          <van-field v-model="publish.message" placeholder="想说点什么..." row="3"/>         
          <van-uploader :after-read="afterRead" v-model="fileList" multiple/>          
          <button class="form-btn">发布</button>
        </van-form>
        
        <div class="listOne" @click="getAddress">
          <img src="../assets/img/person-icon/03.jpg">
          <span>显示位置</span>
          <span class="tag">&gt;</span>
        </div>
        <div class="listTwo" @click="getCollect">
          <img src="../assets/img/person-icon/02.jpg">
          <span>动态权限</span>
          <span class="tag">&gt;</span>
        </div>
    </div>
</template>

<script>
import axios from "axios"
axios.defaults.withCredentials = true
  export default {
    name: "Editor",
      data () {
          return {
            publish:{
              "message": '',
            },
            fileList:[],

          }
      },
      components: {

      },
      methods: {
        onClickLeft() {
            this.$router.push("/collect")
        },
        afterRead(file) {
        // 此时可以自行将文件上传至服务器
          const formData = new FormData();  // 声明一个FormData对象
          formData.append("file", file.file);
          axios
            .post(
              "http://123.57.213.80:7776/saveData",                
            formData,
            {
              headers: {
                "content-type": "multer/form-data"
              }
            }
            )
            .then(res => {
              console.log(res)
            })
            
        },
        onSubmit() {
          let formData = new FormData();
          for(var key in this.publish){
            formData.append(key,this.publish[key]);
          }

          axios({
            method:"post",
            url:"http://123.57.213.80:7776/saveMessage",
            withCredentials:true,
            data:formData,                
          })
            .then(res=>{    
              this.$toast('发布成功')
              this.$router.push("/collect")
          })
        }, 
        getAddress() {
          this.$toast('您的地址为山西省晋中市山西农业大学信息学院')
        },
        getCollect() {
          this.$toast('所有朋友可见')
        }
      },
  }
</script>

<style lang="less" scoped>
