    color: black;
    font-size: .3rem;
  }
  /deep/ .van-nav-bar .van-icon{
      color: black;
  }
  .form-btn{
    width: 5rem;
    height: .8rem;
    border-radius: 1rem;
    background:#80d4fd;
    border: 1px solid  silver;
    color: white;
    font-size: .3rem;
    margin-left: .7rem;
  }
  /deep/ .van-cell {
    height: 2rem;
  }

  .listOne{
    width: 100%;
    height: 1rem;
    border-bottom: 1px #e6e9ec solid;
    line-height: 1rem;
    img{
      display: inline-block;
      float: left;
      margin-left: .3rem;
      margin-top: .15rem;
      width: .6rem;
      height: .7rem;
    }
    span{
      display: block;
