      margin-right: .25rem;
    }
    
  }
  .listTwo{
    width: 100%;
    height: 1rem;
    border-bottom: 1px #e6e9ec solid;
    line-height: 1rem;
    img{
      display: inline-block;
      float: left;
      margin-left: .33rem;
      margin-top: .15rem;
      width: .6rem;
      height: .7rem;
    }
    span{
      display: block;
      float: left;
      font-size: .3rem;
      margin-left: .4rem; 
      
    }
    .tag{
      display: block;
      float: right;
      font-size: .4rem;
      margin-right: .25rem;
    }
    
  }
  .listThree{
    width: 100%;
    height: 1rem;
    border-bottom: 1px #e6e9ec solid;
