import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Person from '../views/Person.vue'
import Collect from '../views/Collect.vue'
import ShopCar from '../views/ShopCar.vue'
import CallCard from '../views/CallCard.vue'
import Login from '../views/Login.vue'
import Sign from '../views/Sign.vue'
import Publish from '../views/Publish.vue'
import Setting from '../views/Setting.vue'
import Test from '../views/Test.vue'
import MyConcern from '../views/MyConcern.vue'
import MyGood from '../views/MyGood.vue'
import MyCollection from '../views/MyCollection.vue'

Vue.use(VueRouter)

    const routes = [
      
    {
      path: '/',
      name: 'Login',
      component: Login,
    },
    {
      path: '/sign',
      name: 'Sign',
      component: Sign,
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,     
        children: [
          {
            path: '/home/footerBar',
            name: 'FooterBar',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ '../components/FooterBar.vue')
          }
        ]
    },
    {
      path: '/person',
      name: 'Person',
      component: Person,
    },
    {
      path: '/setting',
      name: 'Setting',
      component: Setting,
    },
    {
      path: '/collect',
      name: 'Collect',
      component: Collect,
    },
    {
      path: '/publish',
      name: 'Publish',
      component: Publish,
    },
    {
      path: '/shopCar',
      name: 'ShopCar',
      component: ShopCar
    },
    {
      path: '/callCard',
      name: 'CallCard',
      component: CallCard
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/myConcern',
      name: 'MyConcern',
      component: MyConcern
    },
    {
      path: '/myCollection',
      name: 'MyCollection',
      component: MyCollection
    },
    {
      path: '/myGood',
      name: 'MyGood',
      component: MyGood
    },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
