      </template>
    </van-nav-bar>


    <div class="img">
      <van-uploader  v-model="files" multiple :after-read="afterRead" :max-count="1" class="sc" >
        <van-image
          round
          width="1.5rem"
          height="1.5rem"
          :src="avatar"
          ref="headerImg"
        />
      </van-uploader>
      <span>{{username}}</span>
      <input type="text" class="ha_bor" @click="selfBtn" :placeholder="self.sign" v-model="self.sign">
      <button v-if="isShowB2" @click="btnSure">确定</button>
      <!-- <p>夏天的风我永远记得</p> -->

      
    </div>
    <!-- 修改按钮 -->
    <button class="btn" @click="updateImg" v-show="isShow">确定修改</button>

    <!-- 中间部分 -->
