          <div class="one"></div>
        </van-col>
        <van-col span="6" @click="myCollection">
          <span >26</span>
          <span>我的动态</span>
        </van-col>
      </van-row>
    </div>

    <!-- 下面列表  -->
    <div class="listOne" @click="getHealth">
        <img src="../assets/img/person-icon/01.jpg" alt="">
        <span>健康记录</span>
        <span class="tag">&gt;</span>
    </div>

    <div class="listTwo" @click="getCollect">
        <img src="../assets/img/person-icon/02.jpg" alt="">
        <span>我的收藏</span>
        <span class="tag">&gt;</span>
    </div>
