<script>
import FooterBar from '@/components/FooterBar.vue'
import axios from "axios"
axios.defaults.withCredentials = true
  export default {
  
    data () {
      return {
        isShow:false,
        isShowB2:false,
        avatar: '',
        username:'',
        personalSignature:'',
        files:[], 
        self:{
          sign:"个性签名"
        }      
      }
    },
    components: {
      FooterBar
    },
    methods: {
      onClickRight() {
        this.$router.push("/setting")          
      },
      
      afterRead(file) {
        // 此时可以自行将文件上传至服务器
        console.log(file);
        const formData = new FormData();  // 声明一个FormData对象
        formData.append("file", file.file);
        axios
            .post(
              "http://123.57.213.80:7776/upload",
               
            formData,
            {
              headers: {
                "content-type": "multer/form-data"
                }
            }
            )
            .then(res => {
              console.log(res)
              this.isShow = true
              // this.avatar = res.data.info;
              // this.$refs.headerImg.src = userInfo.avatar
              // this.userInfo.avatar = res.config.url
              // console.log(this.userInfo.avatar);
            })
            
      },
      
      updateImg () {
        axios.get("http://123.57.213.80:7776/show").then(res=>{         
          this.avatar = res.data
          this.isShow = false

        }).catch(err=>{
            console.log(err);
        })
              
      },
      selfBtn() {
        this.isShowB2 = true
      },
      btnSure(){
            console.log(self.selfText)
            let formData2 = new FormData();
            for(var key in this.self){
                formData2.append(key,this.self[key]);
            }
        
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/doSign",
                withCredentials:true,
                data:formData2
            })
            .then(res=>{                    
                this.isShowB2 = false                  
            })
      },
      myConcern(){
        this.$router.push("/myConcern")
      },
      myGood(){
        this.$router.push("/myGood")
      },
      myCollection(){
        this.$router.push("/myCollection")
      },

      getHealth(){
        this.$toast('您的上次的健康指数为85.0，身体的健康状况为良好')
      },
      getCollect(){
        this.$toast('您刚刚收藏了一份防疫动态')
      },
      getAddress(){
        this.$toast('您的地址为山西省晋中市山西农业大学信息学院')
      },
      getRequest(){
        this.$toast('您刚刚回答了小帅的问题')
      },
      getInvate(){
        this.$toast('每邀请一位新用户，将得到一份精美礼品')
      },
    },
    mounted () {
      axios.get("http://123.57.213.80:7776/showUsername").then(res=>{
        console.log(res.data)
        this.username = res.data
      })
      axios.get("http://123.57.213.80:7776/show").then(res=>{
        this.avatar = res.data
      })
      axios.get("http://123.57.213.80:7776/showSign").then(res=>{
        this.self.sign = res.data
      })
    },

        
     
}

</script>

