<template>
    <div class="sign">
        <!-- 头部 -->
        <div class="header" :style="headerImg">

        </div> 
        
        <!-- 信息栏 -->
        
        <van-form @submit="onSubmit">
            <van-field                    
                v-model="formMess.username"
                type="username"
                name="username"
                label="用户昵称"
                left-icon="user-circle-o"
                placeholder="请输入用户昵称"
                :rules="[{ required: true, message: '请填写用户昵称' }]"
            />
            <van-field
                v-model="formMess.tel"
                type="tel"
                name="tel"
                left-icon="phone-circle-o"
                label="手机号"
                placeholder="请输入手机号"
                :rules="[{ required: true, message: '请填写手机号' }]"
            />
            <van-field
                v-model="formMess.password"
                type="password"
                name="password"
                left-icon="closed-eye"
                label="密码"
                placeholder="请输入密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <van-field
                v-model="formMess.email"
                type="email"
                name="email"
                left-icon="envelop-o"
                label="邮箱"
                placeholder="请输入邮箱"
                :rules="[{ required: true, message: '请填写邮箱' }]"
            />
            <van-field
                v-model="formMess.authcode"
                center
                clearable
                left-icon="browsing-history-o"
                label="邮箱验证码"
                placeholder="请输入邮箱验证码"
                >
                <template #button>
                    <van-button size="small" type="primary" color="#80d4fd" @click="sendCode">发送验证码</van-button>
                </template>
                </van-field>
            <button class="form-btn">立即注册</button>
        </van-form>
        <div class="mid">

            <router-link to="/">返回登陆</router-link>
        </div>
        
        <div class="end">

        </div>
    </div>
</template>

<script>
import axios from "axios"
export default {
    data () {
        return {
            formMess:{
                "username":"",
                "tel":"",
                "password":"",
                "email":"",
                "authcode":"",
            },

            // username:'',
            // tel: '',
            // password: '',
            // email:'',
            value:'',
            headerImg:[
                {
                    backgroundImage:"url("+require("../assets/img/login/login.jpg")+")",
                    backgroundRepeat: "no-repeat",
                    backgroundSize: "100% 100%",
                },
            ],
        }
    },
    components: {

    },
    methods: {
        onSubmit() {
            /* formData格式提交： */
            let formData1 = new FormData();
            for(var key in this.formMess){
                formData1.append(key,this.formMess[key]);
            }
        
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/doRegister",
                withCredentials:true,
                data:formData1
            })
            .then(res=>{    
                if(res.data.success){
                    // this.$router.push("/home")          
                    this.$toast('注册成功')
                }            
            })
            
        },
        sendCode(){
            let formData1 = new FormData();
            for(var key in this.formMess){
                formData1.append(key,this.formMess[key]);
            }
        
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/getAuthcode",
                withCredentials:true,
                data:formData1
            })

            axios.get("http://123.57.213.80:7776/getAuthcode").then(res=>{
                console.log("1")
            })
        }
    }
}
</script>

<style lang="less" scoped>
    .sign{
        background: #eef9ff;
    }
    .header{
        width: 100%;
        height: 3rem;
        // background: #9ed4ff;
    }
    .form-btn{
        width: 5rem;
        height: .8rem;
        border-radius: 1rem;
        background:#80d4fd;
        border: 1px solid  silver;
        color: white;
        font-size: .3rem;
        margin-left: .7rem;
    }
    /deep/ form {
        margin-top: .7rem;
    }
    /deep/ .van-cell{
        border-radius: 1rem;
        color: gray;
        background-color:#eef9ff;
    }
    /deep/ .van-field__left-icon .van-icon, .van-field__right-icon .van-icon{
        font-size: .6rem;
    }
    // 中间的注册
    .mid{
        width: 100%;
        height: .5rem;
        font-size: .25rem;
        text-align: center;
        color: #a8a8a8;
        margin-top: .5rem;
        a{
            color: #67cbe3;
        }
        
    }
    .end{
        width: 100%;
        height: 3.5rem;
    }
    
</style>
