<template>
    <div class="position">
        <!-- <div id='container'></div>
		<div class="info">
			<h4 id='status'></h4><hr>
			<p id='result'></p><hr>
		</div>
	

        <div id="box">
            <div id='time'>上海XXXXXXXXXXX有限公司</div>
            <button id='signbtn' class="layui-btn layui-btn-normal">考勤签到</button><br>
            <div id="place" type="button" class="layui-btn-radius">非办公地点</div>
            <div id='distance'>系统正在定位中</div>
            <div id='location'>系统正在定位中</div>
        </div>	
        <input type="hidden" id="isonoff" name="isonoff" value="true">
        <input type="hidden" id="userid" name="userid" value="0"> -->
        <van-nav-bar
            title="测评"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            
        />
        <div class="content">

            <van-form @submit="onSubmit">
            <van-field name="radio" label="性别" class="form-p">
                <template #input>
                <van-radio-group v-model="radio" direction="horizontal">
                    <van-radio name="1">男</van-radio>
                    <van-radio name="2">女</van-radio>
                </van-radio-group>
                </template>
            </van-field>
            <van-field
                v-model="health.age"
                name="年龄"
                label="年龄"
                placeholder="请输入年龄"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                v-model="health.height"
                name="身高"
                label="身高"
                placeholder="请输入身高(cm)"
                :rules="[{ required: true, message: '请填写身高' }]"
            />
            <van-field
                v-model="health.weight"
                name="体重"
                label="体重"
                placeholder="请输入体重(kg)"
                :rules="[{ required: true, message: '请填写体重' }]"
            />
            <van-field
                v-model="health.sBp"
                name="收缩压"
                label="收缩压"
                placeholder="请输入收缩压"
                :rules="[{ required: true, message: '请填写收缩压' }]"
            />
            <van-field
                v-model="health.dBp"
                name="舒张压"
                label="舒张压"
                placeholder="请输入舒张压"
                :rules="[{ required: true, message: '请填写舒张压' }]"
            />
            <van-field
                v-model="health.vision"
                name="视力"
                label="视力"
                placeholder="请输入视力(0.1~2.1)"
                :rules="[{ required: true, message: '请填写视力' }]"
            />
            <!-- 提交按钮 -->
            <div style="margin: 16px;">
                <button class="form-btn">开始</button>
            </div>
            </van-form>
      </div>
    </div>
</template>

<script>
import axios from "axios"
export default {
    data () {
        return {
          radio: '1',
          health:{
            "age":'',
            "height":'',
            "weight":'',
            "sBp":'',
            "dBp":'',
            "vision":'',
          }
        }
    },
    components: {
        // FooterBar
    },
    methods: {
      onClickLeft() {
            this.$router.push("/callCard")  
      },
      onSubmit() {
        /* formData格式提交： */
        let formData = new FormData();
        for(var key in this.health){
            formData.append(key,this.health[key]);
        }

        axios({
          method:"post",
          url:"http://123.57.213.80:7776/showHealth",
          withCredentials:true,
          data:formData
        })
        .then(res=>{    
          console.log(res.data)       
          // this.$toast('提交成功')  
          // const res = res.data
          this.$dialog.alert({
          title: "测评结果", //加上标题
          message: res.data, //改变弹出框的内容
          // showCancelButton: true //展示取水按钮
          })
          .then(() => { //点击确认按钮后的调用
                  icon: 'https://img.yzcdn.cn/vant/logo.png'
          })
          .catch(() => { //点击取消按钮后的调用
                  console.log("点击了取消按钮噢")
          })
          
        })
        .catch(err=>{                 
            console.log("提交失败")
        })


      },
      
      
    }
  }
</script>

