<template>
    
    <!-- 顶部背景 -->
    <div class="login">

        <div class="header" :style="headerImg">

        </div>  
        <!-- 登录页 用户表单 -->
        
        <van-form @submit="onSubmit">
            <van-field                    
                v-model="tel"
                name="手机号"
                label="手机号"
                left-icon="phone-o"
                placeholder="请输入手机号"
                :rules="[{ required: true, message: '请填写手机号' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="密码"
                left-icon="closed-eye"
                label="密码"
                placeholder="请输入密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <button class="form-btn">立即登录</button>
        </van-form>
        

        <!-- 跳转到注册 -->
        <div class="mid">
            <span>还没有账号？点击</span>
            <router-link to="/sign">立即注册</router-link>
        </div>

        <!-- 底部背景 -->
        <div class="footer" :style="footerImg">

        </div>
        
        <div class="end">

        </div>
        

    </div>

</template>

<script>
import axios from "axios"
// import Toast from "vant"
 export default {
  data() {
    return {
        tel: '',
        password: '',
        value:'',
        headerImg:[
            {
            backgroundImage:"url("+require("../assets/img/login/login.jpg")+")",
            backgroundRepeat: "no-repeat",
            backgroundSize: "100% 100%",
            },
        ],
        footerImg:[
            {
            backgroundImage:"url("+require("../assets/img/login/footer.jpg")+")",
            backgroundRepeat: "no-repeat",
            backgroundSize: "100% 100%",
            },
        ]
    };
  },
  methods: {
    onSubmit() {
        // 1. 获取用户输入的用户名 和密码
        // var u = this.tel
        // var p = this.password
        // 2.创建正贼表达式验证用户名和密码格式是否正确	
        // var reg = /^[a-z0-9_]{3-8}$/i; // i是忽略大小写,g是全局查找
        // if (!reg.test(u)) {
        //     //提示信息
        //     // Toast("用户名格式不正确,请检查");
        //     console.log("用户名格式不正确,请检查")
        // }
        // if (!reg.test(p)) {
        //     // Toast("密码格式不正确");	
        //     console.log("密码格式不正确")
        // }
        //3.发送ajax
        // var url = "http://123.57.213.80:7776/doLogin?" + u + "&upwd=" + p;
        //4. 获取服务器返回结果 
        // axios.get(url).then(result => {
        //     // console.log(result)
        //     // 5.判断提示用户登录结果
        //     tel = this.tel
        //     password = this.password
        //     if (result.data.success) {
        //         // Toast('登录成功');
        //         console.log("登录成功")	
        //         this.$router.push("/home")
        //     }	else {
        //         // Toast("用户名或密码错误")
        //         console.log("用户名或密码错误")	
        //     }
        // })
        axios({
            method: 'post',
            url: 'http://123.57.213.80:7776/doLogin',
            data: this.$qs.stringify({          //axios的post方法访问后端this.qs.stringify(
            tel: this.tel,
            password: this.password
            }),
        })
        .then(res=>{        //请求成功后执行函数
            if(res.data.success){
                console.log("登陆成功")
                this.$router.push("/home")          
                this.$toast('登录成功')
            }else{
                this.$toast('手机或密码错误!')
            }               
        })

        
    },
  },
};
</script>

<style lang="less" scoped>
    .login{
        background-color: #eef9ff;
    }
    .header{
        width: 100%;
        height: 3rem;
        // background: #9ed4ff;
    }
    /deep/ form {
        margin-top: .7rem;
    }
    // 登录按钮
    .form-btn{
        width: 5rem;
        height: .8rem;
        border-radius: 1rem;
        background:#80d4fd;
        border: 1px solid  silver;
        color: white;
        font-size: .3rem;
        margin-left: .7rem;
    }
    /deep/ .van-cell{
        border-radius: 1rem;
