<template>
    <div class="setting">
        <!-- 头部 -->
        <van-nav-bar
            title="设置"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            
        />
        <van-form>
        <!-- 信息栏 -->
            <van-field v-model="formMess.username" type="username" label="修改昵称" placeholder="请输入昵称"/>
            <van-icon name="passed" size="30" class="one" @click="updataUsername"/>
            <van-field v-model="formMess.password" type="password" label="修改密码" placeholder="请输入密码"/>
            <van-icon name="passed" size="30" class="one" @click="updataPassword"/>
            <van-field v-model="formMess.email" type="email" label="修改邮箱" placeholder="请输入邮箱"/>
            <van-icon name="passed" size="30" class="one" @click="updataEmail"/>
        </van-form>

        <button class="form-btn" @click="backLogin">退出登录</button>
    </div>

</template>

<script>
import qs from 'qs'
import axios from "axios"
export default {
    data () {
        return {
            formMess:{
                "username":"",
                "password":"",
                "email":"",
            },
        }
    },
    components: {

    },
    methods: {
        onClickLeft() {
            this.$router.push("/person")  
        },
        backLogin(){
            this.$router.push("/")
        },
        onSubmit(){
            var username = this.username
            var url = "http://123.57.213.80:7776/updateUsername?" + username
            axios.get(url).then(res=>{
                username = this.username
                console.log("修改成功")
            })
        },
        
        updataUsername(){
            let formData = new FormData();
            for(var key in this.formMess){
                formData.append(key,this.formMess[key]);
            }
            // var username = this.username
            // var url = "http://123.57.213.80:7776/updateUsername?" + username
            // axios.get(url).then(res=>{
            //     username = this.username
            //     console.log("修改成功")
            //})
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/updateUsername",
                withCredentials:true,
                data:formData
            })
            .then(res=>{    
                this.$toast('修改成功')
            })
            .catch(err=>{                   
                console.log("修改失败")  //请求错误后执行函数
            })
        },
        updataPassword(){
            let formData2 = new FormData();
            for(var key in this.formMess){
                formData2.append(key,this.formMess[key]);
            }
            // var username = this.username
            // var url = "http://123.57.213.80:7776/updateUsername?" + username
            // axios.get(url).then(res=>{
            //     username = this.username
            //     console.log("修改成功")
            //})
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/updatePassword",
                withCredentials:true,
                data:formData2
            })
            .then(res=>{    
                 this.$toast('修改成功')     
            })
            .catch(err=>{                   
                console.log("修改失败")  //请求错误后执行函数
            })
        },
        updataEmail(){
            let formData3 = new FormData();
            for(var key in this.formMess){
                formData3.append(key,this.formMess[key]);
            }
            // var username = this.username
            // var url = "http://123.57.213.80:7776/updateUsername?" + username
            // axios.get(url).then(res=>{
            //     username = this.username
            //     console.log("修改成功")
            //})
            axios({
                method:"post",
                url:"http://123.57.213.80:7776/updateEmail",
                withCredentials:true,
                data:formData3
            })
            .then(res=>{    
                this.$toast('修改成功')
            })
            .catch(err=>{                   
                console.log("修改失败")  //请求错误后执行函数
            })
        }

        
    }


}
</script>

<style lang="less" scoped>
    .van-nav-bar{
        background-color: #80d4fd;
        height: 1rem;
        line-height: 1rem;
        .van-icon{
        color: black;
        }
    }
    /deep/ .van-nav-bar__title{
        font-size: .4rem;
    }
    /deep/ .van-nav-bar__text{
        color: black;
        font-size: .3rem;
    }
    /deep/ .van-nav-bar .van-icon{
        color: black;
    }

    /deep/ .van-cell{
        width: 80%;
        border-radius:50% ;
    }
    .one{
        // right: .5rem;
        margin-left: 5.5rem;
        margin-top: -1rem;
    }

    .form-btn{
        width: 5rem;
        height: .8rem;
        border-radius: 1rem;
        background:#80d4fd;
        border: 1px solid  silver;
        color: white;
        font-size: .3rem;
        margin-left: .7rem;
    }
  
  
</style>
